import styled from 'styled-components';
import { keyframes } from 'styled-components';

const SectionInfo = styled.div`
position: absolute;
top: 0;
left: 0;
height: 30px;
min-width: 50px;
background: linear-gradient(90deg, rgba(51,0,255,1) 15px, black 15px);

display: flex;
padding-right: 10px;
padding-left: 25px;
align-items: center;
color: white;
font-weight: bold;
transition: background 2s ease-in-out;
&:hover {
    cursor: pointer;
    background: linear-gradient(90deg, rgba(51,0,255,1) 100%, black 15px);
}
`;

const SectionTitle = styled.div`
position: absolute;
width: calc(100% - 50px);
height: 100px;
background-color: rgba(0,0,0, 0.3);
bottom: 0;
display: flex;
align-items: center;
padding-left: 50px;
font-size: 26px;
font-weight: bold;
color: white;

`;

const BigPanelWrapper = styled.div`
position: relative;
display: block;
background-color: rgba(0,0,0, 0.3);
box-sizing: border-box;
width: calc(50% - 20px);
margin: 10px;
height: calc(100% - 20px);
`;

const BigPanelEmbedInfo = ({section, title}) => {
    return (
        <BigPanelWrapper>
            <SectionInfo>{section}</SectionInfo>
            <SectionTitle>{title}</SectionTitle>
        </BigPanelWrapper>
    )
}

export {
    BigPanelEmbedInfo
}